import React from 'react';
import './category-icon.scss'

function CategoryIcon_({ category }) {

    return (<div className={'category-icon ' + category.sname}>

        <svg className={'center category-icon ' + category.sname}
            viewBox="0 0 100 100"
            preserveAspectRatio="true" >
            <circle className={'category-icon ' + category.sname}
             cx="50" cy="50" r="50" />
        </svg>

        <img className={'center category-icon ' + category.sname}
            src={'categories/' + category.sname + '.svg'}
            alt={category.sname} >
        </img>

    </div>)
}

export default CategoryIcon_
