import React from 'react';
import { connect } from 'react-redux';
import { showDrink } from './drinks-actions'
import DrinksListItem from './DrinksListItem'
import Drink from './Drink'
import './drinks-list.scss'

const stateToProps = state => {
    return {
        drink: state.drinks.drink,
        drinks: state.drinks.drinks
    };
};

function dispatchToProps(dispatch) {
    return { showDrink: drink => dispatch(showDrink(drink)) };
}

function DrinksPrefs({ drink, drinks, showDrink }) {
    return "drinks prefs"
}

export default connect(stateToProps, dispatchToProps)(DrinksPrefs);
